import { Box, Stack } from '@mui/material'
import { useContext, useRef } from 'react'
import { observer } from 'mobx-react-lite'
import QuoteArea from './QuoteArea.js'
import PuzzleCategory from './PuzzleCategory.js'
import HealthBar from './HealthBar.js'
import { StoreContext } from '../store/storeContext.js'
// eslint-disable-next-line no-unused-vars
import styles from './gameplay/gameplay.css'

const GameBoard = (props) => {
  const sliderRef = useRef()
  const [store] = useContext(StoreContext)

  const getSliderYTranslate = () => {
    if (sliderRef.current && store.gameState === 'playing') {
      const sliderHeight = sliderRef.current.offsetHeight
      const halfSliderParentHeight =
        sliderRef.current.parentElement.parentElement.offsetHeight * 0.5 - 64
      return halfSliderParentHeight - 0.5 * sliderHeight
    } else {
      return 5
    }
  }

  const attributionStyle = {
    width: ['100%'],
    px: '5%',
    mt: 1,
    fontFamily: 'Roboto',
    lineHeight: 'normal',
    transition: 'opacity 1700ms ease-in-out',
    opacity: store.gameState === 'succeeded' ? 1 : 0,
    pointerEvents: store.gameState === 'succeeded' ? 'auto' : 'none',
    // don't want attribution to impact slider size calc when hidden
    // using display prop prevents transition from running
    fontSize: store.gameState === 'succeeded' ? '17pt' : '0.1pt',
    textAlign: 'center',
  }

  const sliderStyle = {
    width: ['95%', '80%', '50%'],
    // border: '3px solid pink',
    margin: '0 auto',
    transform: `translateY(${getSliderYTranslate()}px)`,
    transition:
      store.gameState === 'playing' ? '' : 'transform 700ms ease-in-out',
  }

  const debugStyle = {
    fontSize: '10pt',
    position: 'absolute',
    top: 0,
    left: '30%',
  }

  return (
    <Stack
      id='slider'
      ref={sliderRef}
      alignItems='center'
      spacing={0}
      sx={sliderStyle}
    >
      <Box id='debugMsg' sx={debugStyle}>
        {store.test}
      </Box>
      <Box
        id='hudTop'
        sx={{
          /* todo: move these styles to code */ display: 'flex',
          flexDirection: 'row',
          justifyContent: 'space-between',
          flexWrap: 'nowrap',
          width: '100%',
        }}
      >
        <PuzzleCategory sx={{ pl: '4px' }} />
        <HealthBar sx={{ pr: '4px' }} health={store.health} />
      </Box>
      <QuoteArea id='quote' />
      <Box
        sx={{
          // width: ['90%', '80%', '50%'],
          justifyContent: 'right',
        }}
      >
        <Box id='attribution' sx={attributionStyle}></Box>
      </Box>
    </Stack>
  )
}

export default observer(GameBoard)
