import puzzleJson from './puzzleData.json'

let rawPuzzle = {}
let wordList = []
let likelyProperNounMap = {}
let original_word_list = []

const load = async () => {
  const url = 'puzzleData.json'
  //   const puzzles = await fetchJson(puzzleJson)
  const puzzles = puzzleJson
  rawPuzzle = puzzles[Math.floor(Math.random() * puzzles.length)]
  rawPuzzle = puzzles[0]
  updateHyperlinks()
  generateWordList({ fromPositions: true }) // fromPositions shuffle shuffleWithGivens
}

function isPunctuation(str) {
  const t = /[.;:?,]/
  return t.test(str)
}
// todo: rename wordList to wordAndPuncList
const generateWordList = (options) => {
  const puncRegex = /[.;:?,]/g
  const txtWithSpaces = rawPuzzle.text.replace(puncRegex, ' $&')
  wordList = txtWithSpaces.split(' ')

  original_word_list = [...wordList].map((x) => x.toLowerCase())

  // save capitalization info
  // todo: this will simply be baked into the puzzle data
  for (let i = 1; i < wordList.length; i++) {
    // if word is capitlized, but is not first letter or after a period, assume it's propername.
    // Not perfect, breaks on "God is dead."
    const word = wordList[i]
    likelyProperNounMap[word.toLowerCase()] =
      word[0] === word[0].toUpperCase() && wordList[i - 1] !== '.'
  }

  // this will ultimately be removed - puzzles will not be random at runtime
  if (options?.shuffle === true) {
    wordList = shuffle(wordList)
  } else if (options?.shuffleWithGivens === true) {
    wordList = shuffleWithGivens(wordList, rawPuzzle.givens)
  } else if (options?.fromPositions === true) {
    wordList = fromPositions(wordList, rawPuzzle.positions)
  }
}

const fromPositions = (wordList, positions) => {
  const positionedList = []
  for (let iPos = 0; iPos < positions.length; iPos++) {
    positionedList.push(wordList[positions[iPos]])
  }

  return positionedList
}

const shuffleWithGivens = (wordList, givens) => {
  // shuffle words, leaving punctuation and givens in place
  const movableIndexes = Array.from(Array(wordList.length).keys()).filter(
    (w) => !givens.includes(w) && !isPunctuation(wordList[w])
  )
  while (true) {
    const shuffledMovableIndexes = [...movableIndexes]
    shuffledMovableIndexes.sort((a, b) => 0.5 - Math.random())
    const shuffledWords = []
    const shuffledIndexes = []
    for (let i = 0; i < wordList.length; i++) {
      const isMovable = movableIndexes.includes(i)
      const newWordIndex = isMovable ? shuffledMovableIndexes.pop() : i
      const newWord = wordList[newWordIndex]
      shuffledWords.push(newWord)
      shuffledIndexes.push(newWordIndex)
    }

    // ensure no extra correct words beyond the givens
    const correctCount = getCountOfCorrectWords(shuffledWords)
    const countOfPunc = shuffledWords.filter((w) => isPunctuation(w)).length
    if (correctCount === countOfPunc + givens.length) {
      console.log('*'.repeat(50))
      console.log(`${wordList.length}: ${wordList.toString()}`)
      console.log(`${wordList.length}: ${shuffledWords.toString()}`)
      console.log(shuffledIndexes.toString())
      console.log('*'.repeat(50))
      return shuffledWords
    }
  }
}

const shuffle = (words) => {
  // shuffle words, leaving punctuation in place
  const nonPunctuationIndexes = Array.from(
    Array(wordList.length).keys()
  ).filter((w) => !isPunctuation(wordList[w]))
  const shuffledIndexes = [...nonPunctuationIndexes]
  shuffledIndexes.sort((a, b) => 0.5 - Math.random())
  const shuffledWords = []
  for (let i = 0; i < wordList.length; i++) {
    const isPunc = !nonPunctuationIndexes.includes(i)
    const newWord = isPunc ? wordList[i] : wordList[shuffledIndexes.pop()]
    shuffledWords.push(newWord)
  }
  console.log(shuffledWords)
  return shuffledWords
}

const updateHyperlinks = () => {
  rawPuzzle.attribution.replace('<a href=', "<a target='_blank' href=")
}

const fetchJson = async (url) => {
  try {
    const data = await fetch(url)
    const json = await data.json()
    return json
  } catch (error) {
    console.log(error)
  }
}

function getCountOfCorrectWords(words) {
  let correctCount = 0
  for (let i = 0; i < words.length; i++) {
    const correct = isInCorrectLocation(words[i], i)
    correctCount += correct ? 1 : 0
  }

  return correctCount
}

const isInCorrectLocation = (word, index) => {
  return original_word_list[index] === word.trim().toLowerCase()
}

const requiresCapitalization = (txt) => {
  return likelyProperNounMap[txt]
}

const getStartingHealth = () => {
  return rawPuzzle.health
}

export {
  load,
  wordList,
  isPunctuation,
  isInCorrectLocation,
  requiresCapitalization,
  getCountOfCorrectWords,
  getStartingHealth,
  rawPuzzle,
}
