import { useContext } from 'react'
import { StoreContext } from '../store/storeContext.js'
import {
  Drawer,
  List,
  ListItem,
  ListItemButton,
  ListItemText,
  ListItemIcon,
  Typography,
} from '@mui/material'
import SendIcon from '@mui/icons-material/Send'
import RefreshIcon from '@mui/icons-material/Refresh'

const drawerWidth = 250

const CustomDrawer = (props) => {
  const [store] = useContext(StoreContext)

  const listItems = [
    {
      text: 'Refresh puzzle',
      icon: <RefreshIcon />,
      handler: () => store.setPuzzleRefresh(true),
    },
    {
      text: 'Send feedback',
      icon: <SendIcon />,
      handler: props.sendFeedbackHandler,
    },
    {
      text: 'Do another thing',
      icon: <SendIcon />,
      handler: props.sendFeedbackHandler,
    },
  ]

  return (
    <Drawer
      id='drawyer'
      variant='temporary'
      anchor='left'
      sx={{
        width: drawerWidth,
        flexShrink: 0,
      }}
      PaperProps={{
        sx: {
          width: drawerWidth,
          pt: 8,
        },
      }}
      open={props.drawerOpen}
      onClose={props.toggleDrawerCallback}
    >
      <List>
        {listItems.map((item) => (
          <ListItem
            key={item.text}
            disablePadding
            onClick={() => {
              item.handler()
              props.toggleDrawerCallback()
            }}
          >
            <ListItemButton>
              <ListItemIcon>{item.icon}</ListItemIcon>
              <ListItemText primary={item.text} />
            </ListItemButton>
          </ListItem>
        ))}
      </List>
      <Typography
        align='right'
        // sx={{ p: 2, background: ['red', 'blue', 'green', 'orange', 'gray'] }}
        sx={{ p: 2 }}
      >
        v0012
      </Typography>
    </Drawer>
  )
}

export default CustomDrawer
