import { useContext } from 'react'
import { observer } from 'mobx-react-lite'
import { Box } from '@mui/material'
import { StoreContext } from '../store/storeContext.js'

const PuzzleCategory = (props) => {
  const [store] = useContext(StoreContext)
  const categoryStyle = {}

  return (
    <Box id='puzzle-category' sx={{ ...categoryStyle, ...props.sx }}>
      title#category {store.puzzleData.id}
    </Box>
  )
}

export default observer(PuzzleCategory)
