import { AppBar, Toolbar, Typography, IconButton } from '@mui/material'
import { HelpOutline, Menu as MenuIcon, Settings } from '@mui/icons-material'

const appBar = (props) => {
  return (
    <AppBar
      position='fixed'
      color='primary'
      sx={{ zIndex: (theme) => theme.zIndex.drawer + 1 }}
    >
      <Toolbar>
        <IconButton
          size='large'
          edge='start'
          onClick={props.toggleDrawerCallback}
          color='inherit'
          aria-label='menu'
          sx={{ mr: 2 }}
        >
          <MenuIcon />
        </IconButton>
        <Typography variant='h6' component='div' sx={{ flexGrow: 1 }}>
          Working Title
        </Typography>
        <IconButton
          size='large'
          edge='start'
          onClick={props.openSettingsCallback}
          color='inherit'
          aria-label='settings'
          sx={{ mr: 1 }}
        >
          <Settings />
        </IconButton>
        <IconButton
          size='large'
          edge='start'
          onClick={props.openHelpCallback}
          color='inherit'
          aria-label='help'
          sx={{ mr: 1 }}
        >
          <HelpOutline />
        </IconButton>
      </Toolbar>
    </AppBar>
  )
}

export default appBar
