import { Box, Typography } from '@mui/material'
import { useContext } from 'react'
import { observer } from 'mobx-react-lite'
import { StoreContext } from '../store/storeContext.js'

const HealthBar = (props) => {
  const [store] = useContext(StoreContext)
  const trophyEmoji = String.fromCodePoint(127942) //'&#xe131;' // i should use my own picture

  const barStyle = {
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'flex-end',
  }

  return (
    <Box id='healthbar' sx={{ ...barStyle, ...props.sx }}>
      {Array.from(Array(store.health).keys()).map((i) => (
        <Typography key={i}>{trophyEmoji}</Typography>
      ))}
    </Box>
  )
}

export default observer(HealthBar)
