import { action, observable, makeAutoObservable } from 'mobx'

class AppStateStore {
  debugText = 'initial value'
  puzzleRefresh = true
  gameState = 'none'
  health = 0
  puzzleData = {}

  constructor() {
    makeAutoObservable(this, {})
  }

  setDebugText(txt) {
    this.debugText = txt
  }

  setPuzzleRefresh(v) {
    this.puzzleRefresh = v
  }

  setGameState(s) {
    this.gameState = s
  }

  setHealth(health) {
    this.health = health
  }

  setPuzzleData(puz) {
    this.puzzleData = puz
  }
}

export default AppStateStore
